@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
  text-align: center;
}

.App-header {
  height: 100px;
  padding: 20px;
  color: white;
  background: #212121;
  background: -webkit-linear-gradient(top, #373737, #212121);
  background: -o-linear-gradient(top, #373737, #212121);
  background: -webkit-gradient(linear, left bottom, left top, from(#373737), to(#212121));
  background: -webkit-linear-gradient(bottom, #373737, #212121);
  background: -o-linear-gradient(bottom, #373737, #212121);
  background: linear-gradient(to top, #373737, #212121);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-title {
  font-size: 2.5em;
}

.App-main {
  background: #f2f2f2;
  padding: 20px;
}

.App-footer {
  height: 50px;
  background: #212121;
}



